//取主题相关数据

//引入axios模块(封装的ajax)
import axios from 'axios';


var $http = axios.create({
	//基本的url
	baseURL: 'https://www.vue-js.com/api/v1',
	timeout: 5000,
	headers: {'X-Custom-Header':'foobar'}
});

// 请求主题列表的方法
function getTopics(options){
	//默认参数
	var newsOptions = Object.assign({
		tab:'all',
		limit:20,//后台默认20条
		page:1,
	},options);


	return $http({
		url: '/topics',
		//参数
		params:newsOptions,
	});
	// console.log('haha');
}
// axios.get('https://www.vue-js.com/api/v1/topics').then((response)=>{
//   this.items = response.data.data;
// });


// 暴露取数据的模块
export {getTopics}


