<template>
  <div class="list">
    <!-- loading组件 -->
    <loading v-if="!items.length"></loading>

    <!-- 主题列表 -->
    <ul class="items">
      <li class="item" v-for="item in items">
        <img v-lazy="item.author.avatar_url" alt="">
        <div class="tab">
          <div class="mo" v-if="item.top">置顶</div>
          <div class="mo" v-else-if="item.good">精华</div>
          <div v-else>{{tabs[item.tab]}}</div>
        </div>
        <div class="title">{{item.title}}</div>
      </li>
    </ul>
  </div>
</template>

<script>
//引入Loading组件
import loading from '@/components/Loading';

// 引入取数据的模块
import {getTopics} from '@/getdata/getTopic.js';

export default {
  name: 'List',
  data () {
    return {
      //主题分类
      tabs:{
        weex:'Weex',
        ask:'问答',
        job:'招聘',
        share:'分享'
      }
    }
  },
  //接收items栏目列表
  props:['items'],
  //加载loading组件
  components:{
    loading
  },
}
</script>

<style scoped lang="scss">
  // 引入公共样式
  @import '../../assets/sass/base.scss';
  // 引主题列表样式
  @import '../../assets/sass/itemlist.scss';
</style>


