<template>
  <div class="Code">
    <h1>公众号</h1>
    <img src="../../static/images/code.jpg" alt="">
  </div>
</template>

<script>
export default {
  name: 'Code',
  data () {
    return {
      msg: 'Welcome to Your Vue.js App'
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">
  // 引入公共样式
  @import '../assets/sass/base.scss';
  
  .Code{
  	h1{
  		text-align: center;
  		line-height: rem(150px);
  		color: #333;
  	}
  	img{
  		width: rem(600px);
  		height: rem(600px);
  		display: block;
  		margin: 0 auto;
  	}
  }
</style>



