import Vue from 'vue'
import Router from 'vue-router'
import Index from '@/components/Index'
import Code from '@/components/Code'
import User from '@/components/User'
// 引入主题相关的组件
import All from '@/components/topics/All'
import Good from '@/components/topics/Good'
import Share from '@/components/topics/Share'
import Weex from '@/components/topics/Weex'
import Ask from '@/components/topics/Ask'
import Job from '@/components/topics/Job'

// 加载loading
//import Loading from '@/components/Loading'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/index',
      name: 'Index',
      component: Index,
      // 配二级路由
      children:[
        {
          path: '/',
          component:All
        },
        {
          path: 'good',
          component:Good
        },
        {
          path: 'weex',
          component:Weex
        },
        {
          path: 'share',
          component:Share
        },
        {
          path: 'ask',
          component:Ask
        },
        {
          path: 'job',
          component:Job
        }
      ]
    },
    {
      path: '/code',
      name: 'Code',
      component: Code
    },
    {
      path: '/user',
      name: 'User',
      component: User
    },
    // 只是为了调整loading样式加上的路由
    // {
    //   path: '/loading',
    //   component: Loading
    // },
    // 路由跳转
    {
      path:'*',
      redirect:'/index'
    }
  ]
})





