<template>
  <div class="User">
    <h1>我们</h1>
    <div class="play">
	    <!-- Swiper -->
	  	<div class="swiper-container">
		    <div class="swiper-wrapper">
		      <div class="swiper-slide"><img src="../../static/images/p1.jpg" alt=""></div>
		      <div class="swiper-slide"><img src="../../static/images/p2.jpg" alt=""></div>
		      <div class="swiper-slide"><img src="../../static/images/p3.jpg" alt=""></div>
		      <div class="swiper-slide"><img src="../../static/images/p4.jpg" alt=""></div>
		      <div class="swiper-slide"><img src="../../static/images/p5.jpg" alt=""></div>
		    </div>
		    <!-- Add Pagination -->
		    <div class="swiper-pagination"></div>
		  </div>
	  	</div>
 	</div>
</template>

<script>
//引入swiper组件
import Swiper from 'swiper';
import "Swiper/dist/css/swiper.min.css";
export default {
  name: 'Code',
  data () {
    return {
      msg: 'Welcome to Your Vue.js App'
    }
  },
  //钩子函数
  mounted:function(){
  	var swiper = new Swiper('.swiper-container', {
      pagination: {
        el: '.swiper-pagination',
      },
    });
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">
  // 引入公共样式
  @import '../assets/sass/base.scss';

  .User{
  	h1{
  		text-align: center;
  		line-height: rem(150px);
  		color: #333;
  	}
  	.play{
  		margin-top: rem(20px);
  		.swiper-container{
  			img{
  				width: 100%;
  			}
  		}
  	}
  }
</style>



