<template>
  <div class="good">
    <!-- 主题列表 -->
    <topic-list :items="items"></topic-list>
    <!-- 菊花小图标 -->
    <infinite-loading v-if="isShow" @infinite="infiniteHandler"></infinite-loading>
  </div>
</template>

<script>

//引入主题列表组件
import topicList from '@/components/topics/List';

// 引入取数据的模块
import {getTopics} from '@/getdata/getTopic.js';

//引入分页滑动组件
import InfiniteLoading from 'vue-infinite-loading';

export default {
  name: 'Good',
  data () {
    return {
      //主题列表
      items:[],
      //默认显示第一页
      page:1,
      // 用变量标记是否显示隐藏 loading图标
      isShow:true

    }
  },
  

  //钩子函数
  mounted:function(){
    getTopics({tab:'good',limit:10,page:this.page}).then((response)=>{
      console.log(response);
      this.items = response.data.data;
    });
  },

  methods:{
    infiniteHandler($state){
      setTimeout(()=>{
        //查询全部主题数据
        getTopics({tab:'good',limit:10,page:++this.page}).then((response)=>{
          //把查询到的数据追加到items里  concat连接
          this.items = this.items.concat(response.data.data); 
          // 判断下一页还有没有数据 如果没有数据 就是0 直接结束 
          if(response.data.data.length == 0){
            this.isShow = false;
            return;
          }
          $state.loaded();
        })
      },1000);
    },
  },
  //加载组件
  components:{
    topicList,InfiniteLoading
  }
}
</script>

<style scoped lang="scss">
 // 引入公共样式 
 @import '../../assets/sass/base.scss';
 .good{
  margin-top: rem(90px);
  margin-bottom: rem(120px);
 }
</style>




