<template>
  <div id="app">
  	<!-- <p>{{resPath}}</p>
  	<h3>{{$route.path}}</h3> -->
    <router-view></router-view>
    <!-- 底部导航 -->
    <nav class="footer">
    	<ul>
    		<router-link to="/" tag="li" :class="resPath=='index'?'mo':''">
    			<span class="iconfont icon-home_light"></span>
    			<p>首页</p>
    		</router-link>
    		<router-link to="/code" tag="li" :class="resPath=='code'?'mo':''">
    			<span class="iconfont icon-message_light"></span>
    			<p>公众号</p>
    		</router-link>
    		<router-link to="/user" tag="li" :class="resPath=='user'?'mo':''">
    			<span class="iconfont icon-wode"></span>
    			<p>我们</p>
    		</router-link>
    	</ul>
    </nav>
  </div>
</template>

<script>
export default {
  name: 'App',
  // 计算属性
  computed:{
  	resPath:function(){
  		// console.log(this.$route.path);
  		var arr = this.$route.path.split('/');
  		// console.log(arr)
  		return arr[1];
  	}
  }
}
</script>

<style lang="scss">
	// 引入公共样式
	@import './assets/sass/base.scss';
	/*导航*/
	nav.footer{
		position: fixed;
		left: 0;
		bottom: 0;
		width: 100%;
		height: rem(100px);
		background: #315f36;
		ul{
			display: flex;
			li{
				color: #fff;
				width: 33.3%;
				text-align: center;
        height: rem(100px);
				span{
					display: block;
					font-size: rem(50px);
				}
				p{
					font-size: rem(30px);
				}
				&.mo{
					background: #609445;
          height: rem(100px);
				}
			}
		}
	}



</style>



